<template>
  <div>
    <el-tabs :tab-position="tabPosition">
      <el-tab-pane label="">
        <el-radio v-model="proxy" label="global"></el-radio>
        <el-radio v-model="proxy" label="pac">pac</el-radio>
        <el-radio v-model="proxy" label="off"></el-radio>
      </el-tab-pane>
      <el-tab-pane label="()">
        <el-time-select placeholder="" v-model="startTime" :picker-options="{start: '00:00', step: '00:15', end: '23:45'}">
        </el-time-select>
        <el-time-select placeholder="" v-model="endTime" :picker-options="{ start: '00:00',step: '00:15',end: '23:45'}">
        </el-time-select>
        <el-button @click="selectNode"></el-button>
        <p>{{night?night.name:''}}</p>
      </el-tab-pane>
      <el-tab-pane label="">
        <label></label>
        <el-switch v-model="login" active-color="#13ce66" inactive-color="#ff4949" @change="changeLogin" />
        <p>~</p>
      </el-tab-pane>
      <el-tab-pane label="" class="listen">
        <label for="socks">socks:</label>
        <el-input v-model="listen[0]" name="socks" />
        <label for="http">http:</label>
        <el-input v-model="listen[1]" name="http" />
        <label for="_pac">pac:</label>
        <el-input v-model="listen[2]" name="_pac" />
        <el-button @click="portReset"></el-button>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-switch style="display: block" v-model="isIP" @change="setOther('isIP')" active-color="#f1ff11" inactive-color="#114949" active-text="ip" inactive-text="" /><br/> reuse_session:
        <el-switch v-model="reuse_session" label="reuse_session" @change="setOther('reuse_session')" /><br/><br/> reuse_port:
        <el-switch v-model="reuse_port" label="reuse_port" @change="setOther('reuse_port')" /><br/><br/> fast_open:
        <el-switch v-model="fast_open" label="fast_open" @change="setOther('fast_open')" /><br/><br/>
        <span>~</span>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import {
    calcTime
  } from '../utils/time'
  import {
    clearTimeout
  } from 'timers';
  export default {
    data() {
      return {
        tabPosition: 'right',
        mode: null,
        login: false,
        proxy: null,
        startTime: null,
        endTime: null,
        night: null,
        pid1: null,
        pid2: null,
        listen: [],
        isIP: true,
        reuse_port: false,
        fast_open: false,
        reuse_session: true
      }
    },
    mounted() {
      ipc.send('get-login')
      ipc.send('getConf')
      ipc.on('config', (e, conf) => {
        let {
          proxy,
          night,
          mode,
          time: {
            startTime,
            endTime
          },
          listen
        } = conf
        this.proxy = proxy
        this.startTime = startTime
        this.endTime = endTime
        this.night = night
        this.mode = mode
        this.listen = listen
      }).on('login', (e, login) => {
        this.login = login
      }).on('mode', () => {
        ipc.send('getConf')
        if (this.$global.link) ipc.send('link')
      })
      setTimeout(() => {
        this.openNight({
          startTime: this.startTime,
          endTime: this.endTime
        })
      }, 1000)
    },
    watch: {
      proxy: function(newval, old) {
        if (!old) return;
        ipc.send('setConf', {
          proxy: newval
        })
        if (this.$global.link) setTimeout(() => {
          ipc.send('link')
        }, 1000)
      }
    },
    methods: {
      changeLogin(e) {
        ipc.send('set-login', e)
      },
      selectNode() {
        if (this.$global.pid1) {
          window.clearTimeout(this.$global.pid1)
          this.$global.pid1 = null
        }
        if (this.$global.pid2) {
          window.clearTimeout(this.$global.pid2)
          this.$global.pid2 = null
        }
        const time = {
          startTime: this.startTime,
          endTime: this.endTime
        }
        ipc.send('setConf', {
          time
        })
        if (!this.night) {
          return this.$message('~')
        }
        const tem = this.openNight(time)
        tem && this.$message('')
      },
      timer(mode, timeout, cb) {
        return window.setTimeout(() => {
          ipc.send('change-mode', mode)
        }, timeout);
      },
      openNight(time) {
        if (this.$global.pid1 || this.$global.pid2) return false;
        const [isOpen, last, night = 0] = calcTime(time)
        if (last === 0) {
          this.$message('')
          return false
        }
        console.log('time.js', isOpen, last, night)
        night && this.mode === 'night' && this.timer('day', 0)
        this.$global.pid1 = this.timer('night', night * 1000)
        this.$global.pid2 = this.timer('day', last * 1000)
        return true
      },
      portReset() {
        const max = 1 << 16
        let isPort = false
        const listen = this.listen.map((v, i) => {
          v = Number(v)
          if (v >= max || v <= 0) {
            this.$message('')
            isPort = true
            return [1080, 1081, 1082][i]
          }
          return v
        })
        if (isPort) return;
        ipc.send('setConf', {
          listen
        })
        if (this.$global.link) setTimeout(() => {
          ipc.send('link')
        }, 1000)
      },
      setOther(type) {
        ipc.send('other', [type, this[type]])
      }
    }
  }
</script>
<style scoped>
  .listen {
    font-size: 14px;
    margin: 0 0 30px 10px;
  }
  .listen button {
    margin-top: 3px;
  }
</style>